    /* border-radius: 2%; */

    height: 30vh;
    overflow-y: auto;
    width: 100%;
}

.article-show-padding {
    border: 1px solid green;
    background-color: white;
    /* border-radius: 0.1%; */
    min-height: 50vh;
    opacity: 1;
}

.article-side-padding {
    /* background-color: rgba(0, 0, 0, 0.2); */
    position: sticky;
    top: 5vh;
}

.right-tag-padding {
    max-height: 20vh;
    overflow-y: auto;
    overflow: hidden;
    /* padding-left: 1vw; */
    padding-right: 1vw;
    /*隐藏滚动条但不取消滚动功能 */
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /*火狐 */
    /* background-color: rgba(255, 255, 255, 0.5); */
    color: white;

    /* border: 1px solid green; */

}

.right-toc-padding {
    padding-right: 1vw;
    padding-left: 1vw;
    max-height: 40vh;

    overflow-y: auto;

    background-color: rgba(255, 255, 255, 0.5);
    color: white;
    position: relative;

    border: 1px solid green;
}


.right-article-padding {

    padding-right: 1vw;
    max-height: 25vh;
    overflow-y: auto;

