  <div v-if="$store.state.user.bg == null || $store.state.user.id == 0" >

    <!-- <img v-if="screenWidth <= 600" src="https://img.alowlife.com/i/2/2023/12/01/sihqe2-2.webp"
      class="img-phone-background">
    <img v-else style="z-index: -10;" src="https://img.alowlife.com/i/2/2023/12/01/sihqe2-2.webp"
      class="img-window-background"> -->
      <img v-if="screenWidth <= 600" src="https://img.alowlife.com/i/3/2024/03/15/wallhaven-438w60-3.webp"
      class="img-phone-background">
    <img v-else style="z-index: -10;" src="https://img.alowlife.com/i/3/2024/03/15/wallhaven-438w60-3.webp"
      class="img-window-background">

    <div
      style="z-index: -9;  opacity: 1; min-height: 100vh; background-color: rgb(0,0,0,0.2);  font-family: 微软雅黑; font-weight:800;">
      <nav>
        <NavBar v-if="isRouterAlive" />
      </nav>
      <router-view v-if="isRouterAlive" />

    </div>
  </div>
  <div v-else class="is_login-div">
    <div>
      <img v-if="screenWidth <= 600" :src="$store.state.user.bg" class="img-phone-background">
      <img v-else :src="$store.state.user.bg" class="img-window-background">
    </div>

    <div style="z-index: -9;  opacity: 1;  font-family: 微软雅黑; font-weight:800;'">
      <nav>
        <NavBar v-if="isRouterAlive" />
      </nav>
      <router-view v-if="isRouterAlive" />

    </div>
  </div>
</template>

<script>

import NavBar from "@/components/NavBar.vue"
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap/dist/js/bootstrap.min"

import { ref, nextTick, provide } from "vue"
// import 'bootstrap';

export default {
  name: "App",
  components: {
    NavBar
  },
  setup() {
    // 局部组件刷新
    const screenWidth = ref("")
    screenWidth.value = 1024;
    screenWidth.value = window.innerWidth
    const isRouterAlive = ref(true);
    const reload = () => {
      isRouterAlive.value = false;
      nextTick(() => {
        isRouterAlive.value = true;
      });
    };
    provide("reload", reload);

    return {
      isRouterAlive,
      screenWidth
    };
  },

}


</script>

