<script>
import { useStore } from 'vuex';
import router from '@/router';
import { ref } from 'vue';
import $ from "jquery"
import MusicView from '@/views/addition/MusicView.vue';
// import { inject } from 'vue';
export default {
    name: "NavBar",
    components: {
        MusicView: MusicView,
    },
    data() {
        return {
            // myLabels: []
        }
    },
    mounted() {





    },
    methods: {

        alert_msg(msg) {
            alert(msg)
        }
    },

    setup() {
        const store = useStore()

        const confirm_user_logout = () => {
            let flag;
            flag = confirm("是否确认注销，将退出登录")
            if (flag == true) {
                userLogout()
            } else {
                return
            }
        }

        const userLogout = () => {

            store.dispatch("logout", {
                success() {
                    router.push({ name: "login" })
                },
                error() {

                }
            }
            )


        }

        const articleList = ref([])

        let currentPage = 1;
        let totalPage = 1;
        let showPage = ref([]);

        const hasTittle = ref("")



        // 检测登录
        if (!store.state.user.is_login) {
            const my_jwt_token = localStorage.getItem("my_jwt_token");
            if (my_jwt_token != null && my_jwt_token != "" && !store.state.user.is_login) {

                store.commit("updateToken", my_jwt_token);
                store.dispatch("getInfo", {
                    success() {
                        // router.push({ name: "home" })
                        getMyLabels();
                    },
                    error() {
                        console.log("拉取用户信息失败")
                    }
                })
            }
        }

        // const reloadPage = inject("reload")
        const show_label_article = (labelName, myArticle) => {

            let userId = store.state.user.id
            // alert(labelName)
            if (store.state.user.id == 0) {
                userId = 1
            }

            // console.log(labelName)
            router.push({
                path: "/label-article-list",
                query: {
                    authorId: userId,
                    myArticle: myArticle,
                    // articleId: articleId
                    labelName: labelName,
                    // needReload: needReload
                }
            })
            // reloadPage()
            // reloadPage()
            // if (needReload) 
        }

        const myLabels = ref([])

        const getMyLabels = () => {
            // if (myLabels.value.length >= 1) return;
            let userId = store.state.user.id
            if (userId == 0) {
                userId = 1
            }
            // console.log("userId : "+userId)

            $.ajax({
                url: store.state.BaseUrl + "/api/label/common/list/" + userId,
                type: "get",
                headers: {
                    Authorization: "Bearer " + store.state.user.token,
                },

                success(resp) {
                    if (resp.code == 1) {

                        myLabels.value = resp.content.filter(item => item.labelName != "golang"
                            && item.labelName != "日常"
                        )


                        // console.log(myLabels)
                    } else {
                        console.log("拉取个人标签失败")
                    }
                },
            })
        }
        // if (store.state.user.is_login)
        setTimeout(getMyLabels(), 2000);




        const show_article = (articleId) => {
            // location.reload()
            hasTittle.value = ""
            // router.push({ path: "/404" })
            router.push({
                path: "/article",
                query: {
                    authorId: store.state.user.id,
                    articleId: articleId
                }
            })
            // reloadPage()

            // router.go(0)
        }

        const update_pages = () => {
            // let max_pages = parseInt(Math.ceil(totalPage / PAGE));
            let max_pages = totalPage;
            console.log("最大页数" + max_pages)

            let new_page = [];
            for (let i = currentPage - 2; i < currentPage + 2; i++) {
                if (i >= 1 && i <= max_pages) {
                    new_page.push({
                        number: i,
                        is_active: i === currentPage ? "active" : "",
                    })
                }
            }

            showPage.value = new_page;

        }

        const click_page = (page) => {
            if (page === -1) {
                page = currentPage + 1;

            } else if (page === -2) {
                page = currentPage - 1;
            }
            let max_pages = totalPage;
            if (page >= 1 && page <= max_pages) {
                console.log("获取页面：" + page)
                pull_page(page);
            }
        }

        const pull_page = (page) => {
            if (hasTittle.value == null || hasTittle.value == "") return;
            currentPage = page;

            $.ajax({
                //url格式：article/list/页码/用户id/是否需要草稿/
                url: store.state.BaseUrl + "/api/article/list/condition",
                type: "post",
                headers: {
                    Authorization: "Bearer " + store.state.user.token,
                },
                data: {
                    hasTittle: hasTittle.value,
                    pageNum: page


                },
                success(resp) {
                    articleList.value = resp.content.articleList;
                    console.log(resp);
                    totalPage = resp.content.totalPage;
                    update_pages();
                    console.log("拉取 " + hasTittle.value + " 文章列表成功")
                },
            })


        }

        // pull_page(currentPage)
        return {
            show_article,
            pull_page,
            confirm_user_logout,
            articleList,
            click_page,
            showPage,
            hasTittle,
            getMyLabels,
            myLabels,
            show_label_article
        }
    }
}


</script>


